@import url("sass-utils/_variables.scss");
@import url("sass-utils/_functions.scss");
@import url("sass-utils/_mixins.scss");
@import url("sass-utils/_helpers.scss");
@import url("base/reset.css");
@import url("base/normalize.css");
@import url("https://fonts.googleapis.com/css2?family=Vazirmatn:wght@100;200;300;400;500;600;700;800;900&display=swap");
.swiper {
  width: 100%;
  height: 100%;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.text-sm {
  font-size: 0.875rem;
  line-height: 1.25rem;
}

.text-lg {
  font-size: 1.125rem;
  line-height: 1.75rem;
}

.text-xl {
  font-size: 1.25rem;
  line-height: 1.75rem;
}

.text-2xl {
  font-size: 1.5rem;
  line-height: 2rem;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

header {
  max-width: 1825px;
  width: 100%;
  height: 90px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  align-items: center;
  position: relative;
  z-index: 30;
}
header .navbar {
  width: 100%;
}
header .navbar-inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}
header .navbar-inner #logo {
  display: block;
  width: 10%;
}
header .navbar-inner .nav {
  display: flex;
  width: 60.85%;
}
header .navbar-inner .nav li {
  margin-right: 38px;
  height: 25px;
}
header .navbar-inner .nav li a {
  text-decoration: none;
  text-transform: uppercase;
  color: #fff;
  font-weight: 600;
}
header .navbar-inner .nav li a span {
  padding-top: 3px;
  background-image: linear-gradient(to top, #c6e413 0, #c6e413 4px, transparent 4px, transparent 100%);
  background-position: 0 100%;
  background-repeat: no-repeat;
  background-size: 0 100%;
  transition: background-size 0.25s ease-out;
}
header .navbar-inner .nav li a span:hover {
  background-size: 100% 100%;
}

body {
  background-color: #1a202c;
}

footer {
  height: 300px;
  position: relative;
  border: 1px solid blue;
}

.slider {
  height: 900px;
  position: relative;
  top: 0;
  width: 100%;
}
.slider .swiper {
  height: 900px;
  position: relative;
  top: 0;
  margin-top: -90px;
  z-index: 9;
}
.slider .swiper-slide-content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 863px;
  margin-left: 100px;
  margin-right: 100px;
}
.slider .swiper-slide-content-area {
  width: 100%;
  height: 863px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.slider .swiper-slide-content-area > div {
  height: 600px;
  display: flex;
  flex-direction: column;
}
.slider .swiper-slide-content-area > div h2 {
  height: auto;
  color: #fff;
  font-size: 82px;
  text-align: left;
  text-transform: uppercase;
}
.slider .swiper-slide-content-area > div .read-more {
  width: 203px;
  height: 80px;
}
.slider .swiper-slide-content-area > div .read-more a {
  font-weight: 800;
}
.slider .swiper-button-prev {
  color: #fff;
  opacity: 0.6;
}
.slider .swiper-button-next {
  color: #fff;
  opacity: 0.6;
}
.slider .swiper-pagination-bullet-active {
  background-color: #c6e413;
}

button {
  display: block;
  border: 1px solid red;
}

a {
  display: block;
}

.primary {
  background-color: #c6e413;
  color: #000;
  border: 2px solid transparent;
  font-size: 14px;
  font-family: Vazirmatn, sans-serif;
  letter-spacing: 0.015em;
  line-height: 1;
  position: relative;
  font-weight: 800;
  vertical-align: middle;
  align-items: center;
  justify-content: space-between;
  width: auto;
  margin: 0;
  text-decoration: none;
  text-transform: uppercase;
  border-radius: 0;
  outline: 0;
  transition: color 0.5s cubic-bezier(0.45, 0, 0.55, 1), background-color 0.5s cubic-bezier(0.45, 0, 0.55, 1), border-color 0.5s cubic-bezier(0.45, 0, 0.55, 1), background-position 0.5s cubic-bezier(0.45, 0, 0.55, 1);
  padding: 26px 60px 22px;
}